@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

$background-lightGrey: rgb(190, 190, 190);
$title-color: rgb(90, 90, 90);
$borger-darkGrey: rgb(50, 50, 50);
$border-lightGrey: rgb(150, 150, 150);
$text-lightGrey: rgb(130, 130, 130);
$text-darkGrey: rgb(40, 40, 40);


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto;
}

.hide {
  display: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.aviatickets {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}